/** @format */
.n-wrapper {
	height: 10vh;
	display: flex;
	justify-content: space-between;
}

.n-left {
	flex: 1 1;
	align-items: center;
	display: flex;
	grid-gap: 2rem;
	gap: 2rem;
}

.n-name {
	font-size: 1.3rem;
	font-weight: bold;
	cursor: pointer;
}

.n-right {
	display: flex;
	flex: 1 1;
	align-items: center;
	justify-content: center;
	font-weight: 400;
}

.n-list {
	flex: 10 1;
}

.n-list > ul {
	display: flex;
	grid-gap: 2rem;
	gap: 2rem;
	margin-right: 4rem;
	cursor: pointer;
}

.n-list > ul > li:hover {
	cursor: pointer;
	color: var(--orange);
}

.n-button {
	flex: 0.5 1;
	width: 5.5rem;
	height: 2.1rem;
	margin-top: 0.7rem;
}
@media screen and (max-width: 480px) {
	.n-list {
		display: none;
	}
	.n-button {
		display: none;
	}
}

/** @format */

.toggle {
	display: flex;
	justify-content: space-between;
	border: 3px solid var(--orange);
	border-radius: 1rem;
	position: relative;
	padding: 2px;
	cursor: pointer;
}
.toggle > * {
	width: 1rem;
	height: 1rem;
	color: var(--orange);
}
.t-button {
	border-radius: 100%;
	background: var(--orange);
	position: absolute;
}

/** @format */

.intro {
	display: flex;
	height: 77vh;
	margin-top: 6rem;
}

.i-left {
	display: flex;
	position: relative;
	flex: 1 1;
	flex-direction: column;
	grid-gap: 2rem;
	gap: 2rem;
}

.i-right {
	flex: 1 1;
	position: relative;
}

.i-right > * {
	position: absolute;
	z-index: 1;
}

.i-right > :nth-child(1) {
	-webkit-transform: scale(0.69);
	        transform: scale(0.69);
	left: -15%;
	top: -9%;
}
.i-right > :nth-child(2) {
	-webkit-transform: scale(0.67);
	        transform: scale(0.67);
	left: -4rem;
	top: -16.2%;
}

.i-right > :nth-child(3) {
	-webkit-transform: scale(1.6);
	        transform: scale(1.6);
	left: 31%;
	margin-top: 10%;
}

.i-right > :nth-child(4) {
	-webkit-transform: scale(0.6);
	        transform: scale(0.6);
	margin-top: -10%;
	margin-left: 14%;
	border-radius: 50%;
	padding: 0;
	height: 150px;
}

.blur {
	position: absolute;
	width: 22rem;
	height: 14rem;
	border-radius: 50%;
	background-color: #edd0ff;
	z-index: -9;
	top: -18%;
	left: 56%;
	-webkit-filter: blur(72px);
	        filter: blur(72px);
}

.i-name {
	display: flex;
	flex-direction: column;
}

.i-name > :nth-child(1) {
	color: var(--black);
	font-weight: bold;
	font-size: 3rem;
}

.i-name > :nth-child(2) {
	color: var(--orange);
	font-weight: bold;
	font-size: 3rem;
}
.i-name > :nth-child(3) {
	color: var(--gray);
	font-weight: 100;
	font-size: 14px;
	margin-top: 1rem;
}

.i-icons {
	margin-top: 3rem;
	display: flex;
	grid-gap: 0rem;
	gap: 0rem;
}

.i-icons > * {
	-webkit-transform: scale(0.5);
	        transform: scale(0.5);
}

.i-icons > *:hover {
	cursor: pointer;
}

.i-button {
	width: 6rem;
	margin-top: 2rem;
}
@media screen and (max-width: 480px) {
	.intro {
		flex-direction: column;
		grid-gap: 7rem;
		gap: 7rem;
		height: 64rem;
	}

	.i-right {
		-webkit-transform: scale(0.8);
		        transform: scale(0.8);
		left: -1rem;
	}
	.i-right .blur {
		display: none;
	}
	.i-right > :nth-child(3) {
		-webkit-transform: scale(1.6);
		        transform: scale(1.6);
		left: 46%;
		margin-top: 15%;
	}

	.floating-div:nth-of-type(1) {
		/* display: none; */
		top: -7rem !important;
	}
}

/** @format */

.floatingdiv {
	justify-content: space-around;
	display: flex;
	background-color: white;
	box-shadow: var(--boxShadow);
	border-radius: 17px;
	align-items: center;
	padding: 0px 28px 0px 0px;
	height: 4.5rem;
}

.floatingdiv > img {
	-webkit-transform: scale(0.4);
	        transform: scale(0.4);
}

.floatingdiv > span {
	font-family: sans-serif;
	font-size: 16px;
	color: black;
}

/** @format */

:root {
	--yellow: #f5c32c;
	--orange: #fca61f;
	--black: #242d49;
	--gray: #788097;
	--blueCard: #ddf8fe;
	--purple: rgb(238 210 255);
	--boxShadow: 0px 19px 60px rgb(0 0 0 / 8%);
	--orangeCard: rgba(252, 166, 31, 0.45);
	--smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

.App {
	padding: 0.5rem 3.5rem;
	overflow: hidden;
	color: #242d49;
	color: var(--black);
}

@font-face {
	font-family: Kuchbhi;
	src: url("/noumanijaz345.github.io/myportfolio/static/media/Gumela Regular.d3e202e3.otf");
}

* {
	font-family: Kuchbhi;
	font-weight: normal;
	font-size: 20px;
	font-style: normal;
}

body {
	margin: 0;
}

.button {
	border-radius: 34px;
	border: none;
	color: white;
	font-size: 16px;
	padding: 11px 26px 11px 26px;
	background: linear-gradient(180deg, #fdc50f 26.71%, #fb982f 99.36%);
	box-shadow: 0px 20px 24px 3px rgba(251, 161, 40, 0.42);
}

.button:hover {
	background: white;
	cursor: pointer;
	color: #fca61f;
	color: var(--orange);
	border: 1px solid #fca61f;
	border: 1px solid var(--orange);
}

@media screen and (max-width: 480px) {
	.App {
		padding: 0.5rem 1rem;
	}
}

/** @format */

.services {
	padding: 0 3rem 0 3rem;
	display: flex;
	height: 90vh;
	margin-bottom: 8rem;
	margin-top: 5rem;
	margin-bottom: 13rem;
}

.awesome {
	display: flex;
	flex-direction: column;
	position: relative;
}

.awesome > :nth-child(1) {
	color: var(--black);
	font-size: 2.5rem;
	font-weight: bold;
}
.awesome > :nth-child(2) {
	color: var(--orange);
	font-size: 2.5rem;
	font-weight: bold;
}

.awesome > :nth-child(3) {
	color: var(--gray);
	font-size: 14px;
	margin-top: 1rem;
}

.s-button {
	width: 8rem;
	height: 2rem;
	margin-top: 2rem;
}

.cards {
	position: relative;
}

.cards > * {
	position: absolute;
}

/*blur*/

.s-blur1 {
	left: -15rem;
	top: 18rem;
}

.s-blur2 {
	left: 20rem;
	top: 1rem;
}
@media screen and (max-width: 480px) {
	.services {
		margin-top: 0;
		flex-direction: column;
		grid-gap: 5rem;
		gap: 5rem;
		height: 66rem;
		padding: 0;
	}

	.cards {
		display: flex;
		flex-direction: column;
		grid-gap: 17rem;
		gap: 17rem;
	}
	.cards > * {
		position: static;
	}
}

/** @format */

.card {
	width: 10rem;
	height: 13rem;
	position: absolute;
	display: flex;
	flex-direction: column;
	grid-gap: 1rem;
	gap: 1rem;
	align-items: center;
	width: 10rem;
	text-align: center;
	background-color: rgba(255, 255, 255, 0.26);
	border: 7px solid var(--orangeCard);
	box-shadow: var(--boxShadow);
	border-radius: 20px;
	padding: 0px 26px 2rem 26px;
}

.card span:nth-of-type(2) {
	color: var(--gray);
	font-size: 16px;
}

.card > img {
	-webkit-transform: scale(0.6);
	        transform: scale(0.6);
	margin-bottom: -2rem;
}
.c-button {
	background: #ffffff;
	box-shadow: 0px 19px 60px rgba(0, 0, 0, 0.08);
	border-radius: 7px;
	border: none;
	padding: 10px;
	font-size: 16px;
	color: #5290fd;
}
@media screen and (max-width: 480px) {
	.card {
		width: auto;
	}
}

/** @format */

.experience {
	display: flex;
	align-items: center;
	justify-content: center;
	grid-gap: 5rem;
	gap: 5rem;
	height: 30vh;
	margin-bottom: 8rem;
}

.achievement {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.circle {
	width: 4rem;
	height: 4rem;
	background: white;
	border-radius: 100%;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 1.5rem;
	font-weight: bold;
	margin-bottom: 2rem;
	color: black;
}

.circle::before {
	border-radius: 100%;
	content: '';
	background-image: linear-gradient(to bottom, #87e6fb 0%, #ffc05c 100%);
	top: -8px;
	left: -8px;
	bottom: -8px;
	right: -8px;
	position: absolute;
	z-index: -1;
	box-shadow: var(--boxShadow);
}

.achievement span:nth-of-type(2) {
	color: var(--orange);
}
@media screen and (max-width: 480px) {
	.experience {
		-webkit-transform: scale(0.7);
		        transform: scale(0.7);
	}
}

/** @format */

.works {
	padding: 0 3rem 0 3rem;
	display: flex;
	height: 90vh;
	margin-top: 4rem;
	/* scroll */
	align-items: center;
}
/* left side */
.awesome {
	display: flex;
	flex-direction: column;
	position: relative;
}

.awesome > :nth-child(1) {
	color: var(--black);
	font-size: 2.5rem;
	font-weight: bold;
}
.awesome > :nth-child(2) {
	color: var(--orange);
	font-size: 2.5rem;
	font-weight: bold;
}
.awesome > :nth-child(3) {
	color: var(--gray);
	font-size: 14px;
	margin-top: 1rem;
}
.s-button {
	width: 8rem;
	height: 2rem;
	margin-top: 4rem;
}

/* right side */
.w-right {
	position: relative;
	left: -12rem;
}

.w-mainCircle {
	left: 22rem;
	position: relative;
	width: 18rem;
	height: 18rem;
	border-radius: 100%;
	box-shadow: var(--smboxShadow);
	top: 2rem;
	background: white;
	/* darkMode */
	z-index: 2;
}
.w-mainCircle > * {
	position: absolute;
}
.w-mainCircle > :nth-child(1) {
	top: -3rem;
	left: 6rem;
}
.w-mainCircle > :nth-child(2) {
	left: -3rem;
	top: 5rem;
}
.w-mainCircle > :nth-child(3) {
	left: 6rem;
	top: 5rem;
}
.w-mainCircle > :nth-child(4) {
	left: 15rem;
	top: 5rem;
}
.w-mainCircle > :nth-child(5) {
	left: 6rem;
	top: 13rem;
}

.w-secCircle {
	width: 6rem;
	height: 6rem;
	position: absolute;
	border-radius: 100%;
	border: 5px solid #ebebeb;
	display: flex;
	align-items: center;
	justify-content: center;
	background: 'none';
	box-shadow: var(--smboxShadow);
	background: white;
}

.w-secCircle > img {
	-webkit-transform: scale(0.6);
	        transform: scale(0.6);
}

.w-backCircle {
	position: absolute;
	height: 13rem;
	width: 13rem;
	border-radius: 100%;
	/* darkMode */
	z-index: 1;
}
.blueCircle {
	background: #1949b8;
	left: 31rem;
	top: 0rem;
}
.yellowCircle {
	background: #f5c32c;
	left: 31rem;
	top: 8rem;
}
@media screen and (max-width: 480px) {
	.works {
		flex-direction: column;
		height: 55rem;
		padding: 0;
		grid-gap: 5rem;
		gap: 5rem;
	}

	.w-right {
		display: flex;
		align-items: flex-start;
		justify-content: start;
		-webkit-transform: scale(0.7);
		        transform: scale(0.7);
		left: -1rem;
	}
	.w-mainCircle {
		position: static;
	}
	.w-backCircle {
		left: 8rem !important;
	}
}

/** @format */

.portfolio {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	height: 70vh;
}
/* heading */
.portfolio span:nth-of-type(1) {
	color: var(--black);
	font-size: 2rem;
	font-weight: bold;
}

.portfolio span:nth-of-type(2) {
	color: var(--orange);
	font-size: 2.5rem;
	font-weight: bold;
}
/* slider */
.portfolio .swiper {
	overflow: visible !important;
}
.portfolio-slider {
	margin-top: 3rem;
	width: 100%;
}
.portfolio-slider .swiper-slide {
	width: 20rem;
}
.portfolio img {
	width: 19rem;
	-webkit-filter: drop-shadow(-12px 15px 13px rgba(0, 0, 0, 0.25));
	        filter: drop-shadow(-12px 15px 13px rgba(0, 0, 0, 0.25));
	border-radius: 19px;
}

/** @format */
.t-wrapper {
	padding: 0 3rem 0 3rem;
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-top: 7rem;
	flex-direction: column;
	grid-gap: 2rem;
	gap: 2rem;
	position: relative;
}
.t-heading {
	align-self: start;
}
.t-heading > * {
	font-size: 2rem;
	font-weight: bold;
}
.t-heading span:nth-of-type(2) {
	color: var(--orange);
}

.t-blur1 {
	left: 45rem;
	top: 16rem;
}
.t-blur2 {
	top: 9rem;
	left: 5rem;
}
.testimonials {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
	padding: 2rem;
	width: 30rem;
	height: 12rem;
	/* styles same as work.css */
	background: rgba(255, 255, 255, 0.26);
	border: 7px solid var(--blueCard);
	box-shadow: var(--boxShadow);
	border-radius: 20px;
}
.testimonials > img {
	width: 6rem;
	height: 6rem;
	border-radius: 100%;
}
.testimonials > span {
	color: var(--gray);
	font-size: 20px;
}

/* slider */
.t-wrapper .swiper {
	width: 100%;
	height: 70%;
}

.t-wrapper .swiper-slide {
	display: flex;
	align-items: center;
	justify-content: center;
}

.t-wrapper .swiper-pagination-bullet-active {
	background: var(--orange);
}
@media screen and (max-width: 480px) {
	.t-wrapper {
		padding: 0;
	}
	.testimonial > img {
		width: 70px;
		height: 70px;
	}
	.testimonials > span {
		font-size: 15px;
	}
}

/** @format */

.contact-form {
	padding: 0 3rem 0 3rem;
	display: flex;
	height: 80vh;
	margin-top: 4rem;
	/* scroll */
	padding-top: 3rem;
}

.c-right {
	display: flex;
	justify-content: center;
	position: relative;
	flex: 1 1;
}

.c-right > form {
	display: flex;
	flex-direction: column;
	grid-gap: 2rem;
	gap: 2rem;
	align-items: center;
}

.c-right .user {
	width: 20rem;
	height: 2rem;
	padding: 0.3em;
	outline: none;
	border: 2px solid var(--orange);
	border-radius: 8px;
	font-size: 16px;
}

textarea {
	height: 4rem !important;
}

.c-blur1 {
	top: 1rem;
	left: 15rem;
}

@media screen and (max-width: 480px) {
	.contact-form {
		padding: 0;
		flex-direction: column;
		grid-gap: 5rem;
		gap: 5rem;
		height: 40rem;
	}
	.c-right .user {
		width: 16rem;
	}
}

/** @format */

.footer {
	display: flex;
	align-items: center;
	margin: -0.5rem -3.5rem;
	position: relative;
	margin-top: -7rem;
}
.f-content {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	width: 100%;
	margin-top: 4rem;
	grid-gap: 4rem;
	gap: 4rem;
	color: white;
}
.f-icons {
	display: flex;
	grid-gap: 2rem;
	gap: 2rem;
}

@media screen and (max-width: 480px) {
	.f-content {
		-webkit-transform: scale(0.5);
		        transform: scale(0.5);
	}
}

